<template>
  <el-tabs v-model="activeName" @tab-click="handleClick">
    <el-tab-pane label="Utilisateur" name="first">
        <UtilisateurPresence  v-bind:usersOption="usersOption" v-bind:userSelect="userSelect" />
    </el-tab-pane>
    <el-tab-pane label="Global Agences" name="second">
        <GbAgencePresence v-bind:agencesOption="agencesOption" v-bind:agenceSelect="agenceSelect" />
    </el-tab-pane>
    <!-- <el-tab-pane label="Global Utilisateurs" name="third">
        <GbutilisateurPresence/>
    </el-tab-pane> -->
  </el-tabs>
</template>
<script>

    // Utilities
    import NProgress from 'nprogress';
    import 'nprogress/nprogress.css';
    import axios from 'axios';

    import UtilisateurPresence from './UtilisateurPresenceComponent.vue';
    import GbAgencePresence from './GbAgencePresenceComponent.vue';
    import GbutilisateurPresence from './GbutilisateurPresenceComponent.vue';

    export default {

        components: {
            UtilisateurPresence,
            GbAgencePresence,
            GbutilisateurPresence
        },

        data() {
        return {
            activeName: 'first',
            agencesOption: [],
            agenceSelect: [],
            usersOption: [],
            userSelect: [],
        };
        },
        methods: {

            handleClick(tab, event) {
                NProgress.start();
                console.log(tab, event);
                NProgress.done();
            },

            async loadFilter()
            {
                NProgress.start();

                try {
                  
                    const response = await axios.get('xperf/report/filter',);
                    var res_agences = response.data.agences;
                    var res_users = response.data.users;

                    var agences = [];
                    var users = [];

                    var array_agence = [];
                    var array_users = [];



                    res_agences.forEach(element => {
                    
                        if (!agences.includes(element.code)) {
                            agences.push(element.code);
                            array_agence.push({
                                label: element.nom,
                                value: element.code
                            });
                        }
                    });

                    res_users.forEach(element => {
                    
                        if (!users.includes(element.id)) {
                            users.push(element.id);
                            array_users.push({
                                label: element.name,
                                value: element.id
                            });
                        }
                    });

                    if(array_agence.length >1){
                       array_agence.unshift({
                                label: 'Tous',
                                value: 'Tous'
                        }); 
                    }
                    this.agencesOption = array_agence;
                    this.agenceSelect = this.agencesOption[0];

                    this.usersOption = array_users;
                    this.userSelect = this.usersOption[0];


                } catch (error) {
                    this.$notify({
                    type: 'error',
                    title: "Echec de recuperation"
                });
                }

                NProgress.done();

            },

        },

        created () {
            this.loadFilter();
        }
    };
</script>